.cardGroup{
    display: flex;
    align-content: space-between;
    flex-direction: column;
    background: white;
    height: 160px; 
    border: 1px solid #ccc;
    border-radius: 20px;
    cursor: pointer;
    padding: 15px;
}
.cardGroup.active{
    display: flex;
    align-content: space-between;
    flex-direction: column;
    background: #161616;
    color: white;
    height: 160px; 
    border: 1px solid #161616;
    border-radius: 20px;
    cursor: pointer;
    padding: 15px;
}


.cardGroup:hover{
    border: 1px solid #161616;
}

.cardGroup i{
    font-size: 2.5rem;
    padding-top: 10px;
    margin-bottom: 15px;
}

.cardGroup p{
    max-width: 140px;
    font-weight: 600;
}

