.swiper {
    width: 100%;
    height: 100%;
  }
  
  .swiper-slide {
    width: 10px;
    height: 100px;
    text-align: center;
    font-size: 18px;
    background: #ffffff;
  
    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    opacity: 0.5;
    border-bottom: solid 2px white;
  }
  .swiper-slide:hover{
    opacity: 1;
    cursor: pointer;
  }
  
  .swiper-slide img {
    display: block;
    width: 30px;
    height: 30px;
    object-fit: cover;
  }
  .swiper-slide span{
    font-size: 12px;
    font-weight: 800;
  }

 .swiper-slide.active{
    opacity: 1;
    cursor: pointer;
    border-bottom: solid 2px black;
 }

.swiper-button-prev::after{
    content: " ";
    width: 8px;
    height: 8px;
    border-bottom: 2px solid black;
    border-right: 2px solid black;
    transform: rotate(135deg);
}

.swiper-button-next::after{
    content: " ";
    width: 8px;
    height: 8px;
    border-bottom: 2px solid black;
    border-right: 2px solid black;
    transform: rotate(-45deg);
}
.swiper-button-next{
    background-color: white;
    width: 35px;
    height: 35px;
    border: 1px solid #ccc;
    border-radius: 35px;
    box-shadow: 5px 10px 20px 30px white;
}
.swiper-button-prev{
    background-color: white;
    width: 35px;
    height: 35px;
    border: 1px solid #ccc;
    border-radius: 35px;
}

.swiper-button-next.swiper-button-disabled{
    opacity: 0;
}
.swiper-button-prev.swiper-button-disabled{
    opacity: 0;
}

.btn-filtro{
  display: none;
}

.btn-filtro:hover{
    border: solid 1px gray;
}

@media screen and (max-width:575px){
  .swiper-button-next{
    display: none;
  }
  .swiper-button-prev{
    display: none;
  }
}